import * as React from 'react';
import { Link } from 'gatsby-plugin-react-i18next';
import * as styles from './header.module.css';
import {Button, Image} from '@fluentui/react-components';
import Logo from '../images/Logo-White-Short.png';
import {useEffect, useRef, useState} from 'react';
import { Navigation24Regular } from '@fluentui/react-icons';
import { useTranslation } from 'gatsby-plugin-react-i18next';
import { navigate } from 'gatsby';

interface HeaderProps {
    siteTitle: string;
}

const Header: React.FC<HeaderProps> = ({ siteTitle }) => {
  const [hidden, setHidden] = React.useState(false);
  const [small, setSmall] = React.useState(false);
  const lastScrollY = React.useRef(0);
  const { t, i18n } = useTranslation();
  const [activeLang, setActiveLang] = useState(i18n.language);

    const ticking = useRef(false); // Prevent too many state updates

    useEffect(() => {
        const handleScroll = () => {
            if (!ticking.current) {
                window.requestAnimationFrame(() => {
                    const currentScrollY = window.scrollY || window.pageYOffset; // Ensure we handle different browsers

                    // iOS "rubber band" effect handling
                    if (currentScrollY < 0) {
                        return; // Don't process negative scroll positions
                    }

                    if (currentScrollY > lastScrollY.current) {
                        setHidden(true);  // User is scrolling down
                    } else {
                        setHidden(false); // User is scrolling up
                    }

                    // Shrink header when scrolling down but reset when back to the top
                    if (currentScrollY > 0) {
                        setSmall(true);
                    } else {
                        setSmall(false);
                    }

                    lastScrollY.current = currentScrollY;
                    ticking.current = false; // Reset the ticking flag
                });
                ticking.current = true;
            }
        };

        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);
  useEffect(() => {
    // console.log("hidden state changed: ", hidden);
  }, [hidden]);

  useEffect(() => {
    // console.log("small state changed: ", small);
  }, [small]);


    const onProjectRequestClick = () => {
        window.location.href = '/project-request';

    };
  // console.log("Component re-rendered");

  const toggleMobileMenu = () => {
    const mobileNav = document.getElementById('mobileNav');
    //mobileNav?.classList.toggle('open');
    if (mobileNav.style.height > '0px') {
        //mobileNav.style.display = 'none';
        mobileNav.style.height = "0px"
        mobileNav.style.borderBottom = "0px solid #D9D9D9"
    } else {
       // mobileNav.style.display = 'block';
        mobileNav.style.height = "300px"
        mobileNav.style.borderBottom = "1px solid #D9D9D9"
    }
};

useEffect(() => {
  const handlePopState = () => {
      const currentPath = window.location.pathname;
      const languageInPath = currentPath.split('/')[1]; // Get language from the URL

      if (['en', 'fr', 'de'].includes(languageInPath) && languageInPath !== i18n.language) {
          // Update the language to match the language in the URL
          i18n.changeLanguage(languageInPath);
      }
  };

  window.addEventListener('popstate', handlePopState);

  return () => {
      window.removeEventListener('popstate', handlePopState);
  };
}, []);


const handleLanguageChange = (lng: string) => {
    console.log("language selected is : " + lng)
    i18n.changeLanguage(lng).then(() => {
        console.log(`Language current one is: ${i18n.language}`);
        const currentPath = window.location.pathname;
        setActiveLang(lng);

    // Check if the current path already includes a language code
    if (currentPath.startsWith('/en') || currentPath.startsWith('/fr') || currentPath.startsWith('/de')) {
      // Replace the language code in the path
      const newPath = currentPath.replace(/^\/(en|fr|de)/, `/${lng}`);
      navigate(newPath, { replace: true });
    } else {
      // If no language code is in the path, prepend the new language code
      const newPath = `/${lng}${currentPath}`;
      navigate(newPath, { replace: true });
    }
    });
  };

    return (
        <header className={`${styles.header} ${hidden ? styles.hidden : ''} ${small ? styles.small : ''}`}>
            <div className={styles.container}>
                <div style={{height: "inherit", display: "flex"}}>
                    <Link to="/" className={styles.title}>
                        <Image src={Logo} alt="Site Logo"/>
                    </Link>
                </div>
                <nav>
                    <ul className={styles.navList}>
                        <li className={styles.navItem}>
                            <Link to="/" className={styles.navLink}>{t('Home')}</Link>
                        </li>
                        <li className={styles.navItem}>
                            <Link to="/services" className={styles.navLink}>{t('Services')}</Link>
                        </li>
                        {/*<li className={styles.navItem}>*/}
                        {/*    <Link to="/project-request" className={styles.navLink}>{t('Request Help')}</Link>*/}
                        {/*</li>*/}
                        <li className={styles.navItem}>
                            <Link to="/contact" className={styles.navLink}>{t('Contact')}</Link>
                        </li>
                    </ul>
                </nav>
                <div style={{display: "flex", justifyContent: "flex-end"}}>
                <button className={styles.button} onClick={onProjectRequestClick}><span>{t('Project Request')}</span></button>

                <div className={styles.languageSwitcher} style={{ marginLeft: "2rem", display: "flex", alignItems: "center" }}>
            <span style={{ cursor: "pointer", margin: "0 0.5rem" }} className={activeLang === 'en' ? styles.active : ''} onClick={() => handleLanguageChange('en')}>
              EN
            </span>
            |
            <span style={{ cursor: "pointer", margin: "0 0.5rem" }} className={activeLang === 'de' ? styles.active : ''} onClick={() => handleLanguageChange('de')}>
              DE
            </span>
            {/* |
            <span style={{ cursor: "pointer", margin: "0 0.5rem" }} className={activeLang === 'fr' ? styles.active : ''} onClick={() => handleLanguageChange('fr')}>
              FR
            </span> */}
          </div>     
                </div>           
            </div>
            <div className={styles.mobileHeader}>
                <div style={{height: "inherit", display: "flex"}}>
                        <Link to="/" className={styles.title}>
                            <Image src={Logo} alt="Site Logo"/>
                        </Link>
                    </div>
                <button className={styles.mobileMenuButton} onClick={toggleMobileMenu}>
                    <span className={styles.hamburgerIcon}><Navigation24Regular/></span>
                </button>
                </div>
                <nav className={`${styles.mobileNav} ${small ? styles.mobileNavSmall : ''}`} id="mobileNav">
                    <ul className={styles.mobileNavList}>
                        <li className={styles.navItem}>
                            <Link to="/" className={styles.navLink}>{t('Home')}</Link>
                        </li>
                        <li className={styles.navItem}>
                            <Link to="/services" className={styles.navLink}>{t('Services')}</Link>
                        </li>
                        {/*<li className={styles.navItem}>*/}
                        {/*    <Link to="/project-request" className={styles.navLink}>{t('Request Help')}</Link>*/}
                        {/*</li>*/}
                        <li className={styles.navItem}>
                            <Link to="/contact" className={styles.navLink}>{t('Contact')}</Link>
                        </li>
                    </ul>
                    <div style={{padding: "1rem 0rem", marginLeft: "2rem"}}>
                <button className={styles.button} onClick={onProjectRequestClick}><span>{t('Project Request')}</span></button>     

                <div className={styles.languageSwitcher} style={{ marginTop: "1rem", display: "flex", alignItems: "start" }}>
            <span style={{ cursor: "pointer", margin: "0 0.5rem" }} className={styles.active} onClick={() => handleLanguageChange('en')}>
              EN
            </span>
            |
            <span style={{ cursor: "pointer", margin: "0 0.5rem" }} onClick={() => handleLanguageChange('de')}>
              DE
            </span>
            {/* |
            <span style={{ cursor: "pointer", margin: "0 0.5rem" }} onClick={() => handleLanguageChange('fr')}>
              FR
            </span> */}
          </div>    
                </div> 
                </nav>
            
        </header>
    );
};

export default Header;